const getFactFromApi = async () => {
    // Make the API request
    const response = await fetch("https://uselessfacts.jsph.pl/api/v2/facts/random", {
        "method": "GET"
    });

    {/* I initially had something cooler with a historical event that would have given you an event that happened on the same day in a random year, reporting the year and event from the API response, but either the API itself or SAIT was swatting that down and not authorizing me to do that, so I went with the safe thing. */}

    const json = await response.json();

    const newFact = json.text;

    return newFact;
}

export { getFactFromApi };
